}

/* '' */
.icon-cancel:before {
  content: '\e802';
}

/* '' */
.icon-bell:before {
  content: '\e803';
}

/* '' */
.icon-search:before {
  content: '\e804';
}

/* '' */
.icon-calendarplus:before {
  content: '\f271';
}

/* '' */
.icon-calendartimes:before {
  content: '\f273';
}

